.App{
  min-height: 100vh;
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.welcome{
  text-align: center;
  padding: 10px;
}

.suggestions{
  text-align: center;
  padding: 10px;
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  padding: 10px;
  border: none;
}

.search-button{
  background:skyblue;
  border: none;
  padding: 10px 20px;
  color: white
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
}

a:link{
  color: white;
  text-decoration: none;
  font-size: 15px;
}

a:visited{
  color:white;
}

@media (max-width: 424px){
  .search-form{
    padding: 10px;
  }
}