.recipe {
    border-radius: 5px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}

.image{
    border-radius: 50%;
    width: 300px;
    height: 300px;
    padding: 10px;
}

.button{
    background: skyblue;
    border: none;
    padding: 10px 20px;
    color: white;
    border-radius: 5px;
    margin: 2em;
}

@media (max-width: 424px){
    .image{
        width: 200px;
        height: 200px;
    }
}